'use strict';

$(document).ready(function () {
  $('.j_Slider').slider({
    loop: false,
    autoPlay: false,
    slide: function(e, index){
      if(index === 1){
        $('.j_LuckyNum').removeClass('hide').addClass('show');
      }else{
        $('.j_LuckyNum').removeClass('show').addClass('hide');
      }
    }
  });
});


